import React, { Component } from "react";
import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";
import './ReporteVentas.css'
import config from '../../../config'
import NumberFormat from 'react-number-format';
import funFormatoDecimal from '../../../Funciones/funFormatoDecimal'
import ModalDetalleVenta from "./Modals/ModalDetalleVenta";

class ReporteVentas extends Component {

    constructor(){
        super();
        this.state ={
            fechaInicio : new Date(),
            fechaFin : new Date(),

            var_numeroVentas     : 0,
            var_totalVentas      : 0,
            var_numeroTarjetas   : 0,
            var_totalTarjetas    : 0,
            var_numeroEfectivo   : 0,
            var_totalEfectivo    : 0,
            var_numeroCancelados : 0,
            var_totalCancelados  : 0,
            var_numeroGastos     : 0,
            var_totalGastos      : 0,
            var_numeroIngresos   : 0,
            var_totalIngresos    : 0,
            var_numeroItemsVendidos    : 0,
            var_numeroItemsCancelados  : 0,

            var_abrirModalDetalleVenta : false
        }
        this.ObtenerReporteVentas = this.ObtenerReporteVentas.bind(this)
        this.formatDate = this.formatDate.bind(this)
    }

    ObtenerReporteVentas(){


        
        fetch(config.api+`/api/mostrar-reporte-ventas`,
            {
                method: 'POST',
                body: JSON.stringify({
                    re_fechainicio : this.state.fechaInicio,
                    re_fechafinal  : this.state.fechaFin,
                }),
                headers: {
                    'Accept' : 'application/json',
                    'Content-Type': 'application/json',
                    'usuid'         : localStorage.getItem('usuid')
                }
            }
        )
        .then(response => response.json())
        .then(data => {
            
            
            if(data['response'] == true){
                
                this.setState({
                    var_numeroVentas : data['rpta_numeroVentas'],
                    var_totalVentas : data['rpta_totalVentas'],
                    var_numeroTarjetas : data['rpta_numeroTarjetas'],
                    var_totalTarjetas : data['rpta_totalTarjetas'],
                    var_numeroEfectivo : data['rpta_numeroEfectivo'],
                    var_totalEfectivo : data['rpta_totalEfectivo'],
                    var_numeroCancelados : data['rpta_numeroCancelados'],
                    var_totalCancelados : data['rpta_totalCancelados'],
                    var_numeroGastos : data['rpta_numeroGastos'],
                    var_totalGastos : data['rpta_totalGastos'],
                    var_numeroIngresos : data['rpta_numeroIngresos'],
                    var_totalIngresos : data['rpta_totalIngresos'],
                    var_numeroItemsVendidos : data['rpta_numeroItemsVendidos'],
                    var_numeroItemsCancelados : data['rpta_numeroItemsCancelados'],
                })

            }else{   

            }
            
        })

    }

    formatDate(date){
        let formatted_date = date.getFullYear() + "/" + (date.getMonth() + 1) + "/" +  date.getDate()
        return formatted_date;
    }
    
    render(){
        return (
            <div>
                <div
                    style={{
                        display:'flex',
                        marginBottom:'20px'
                    }}
                >
                    <div>
                        Fecha Inicio:<br/>
                        <DatePicker 
                            dateFormat="dd/MM/yyyy"
                            selected={this.state.fechaInicio} 
                            locale="pt-BR"
                            onChange={(date, fecha) => {
                                // setFechaInicio(date)
                                this.setState({
                                    fechaInicio: date
                                })
                            }}
                            value
                        />
                    </div>

                    <div
                        style={{
                            marginLeft:'50px'
                        }}
                    >
                        Fecha Fin:<br/>
                        <DatePicker 
                            dateFormat="dd/MM/yyyy"
                            selected={this.state.fechaFin} 
                            onChange={(date, fecha) => {
                                this.setState({
                                    fechaFin: date
                                })
                            }}
                        />
                    </div>

                    <div>
                        <button
                            type="button" 
                            className="btn btn-success btn-rounded btn-fw desactivado"
                            style={{
                                marginLeft:'20px'
                            }}
                            onClick={() => {
                                this.ObtenerReporteVentas()
                            }}
                        >
                            Buscar
                        </button>

                    </div>
                </div>

                {/* <DatePicker
                    selected={startDate}
                    onChange={(date) => setStartDate(date)}
                    customInput={<button>
                        fecha
                    </button>}
                />

                <DatePicker
                    selected={startDate}
                    onChange={(date) => setStartDate(date)}
                    dateFormat="MM/yyyy"
                    showMonthYearPicker
                /> */}

                <div>

                    <div className="card">
                        <div className="card-body">
                            <table id="tb_ventas" className="table table-striped Tabla-RegistroAlmacen" style={{width:'100%'}}>
                                <tbody>
                                    <tr>
                                        <td>FECHA INICIO</td>
                                        <td>
                                            {
                                                 this.state.fechaInicio.getDate()+ "/" + (this.state.fechaInicio.getMonth() + 1) + "/" +  this.state.fechaInicio.getFullYear()
                                            }
                                        </td>
                                        <td 
                                            rowSpan={"2"}
                                            style={{
                                                display: "flex",
                                                justifyContent: "center",
                                                color:'transparent'
                                            }}
                                        >
                                            XX
                                        </td>
                                    </tr>
                                    <tr>
                                        <td>FECHA FIN</td>
                                        <td>
                                            {
                                                 this.state.fechaFin.getDate() + "/" + (this.state.fechaFin.getMonth() + 1) + "/" +  this.state.fechaFin.getFullYear()
                                            }
                                        </td>
                                    </tr>
                                    <tr>
                                        <td>N° VENTAS</td>
                                        <td>{this.state.var_numeroVentas}</td>
                                        <td 
                                            rowSpan={"2"}
                                            style={{
                                                position:'relative'
                                            }}
                                        >
                                            <div
                                                style={{
                                                    height: "100%",
                                                    width: "100%",
                                                    position: "absolute",
                                                    top: "0",
                                                    display: "flex",
                                                    justifyContent: "center",
                                                    alignItems: "center",
                                                }}
                                            >
                                                <button 
                                                    onClick={() => {
                                                        this.setState({
                                                            var_abrirModalDetalleVenta : true
                                                        })
                                                    }}
                                                    className="btn btn-rounded btn-fw btn-primary ver" 
                                                    type="button" >
                                                        <i className="mdi mdi-eye"></i>
                                                </button>
                                            </div>
                                        </td>
                                    </tr>
                                    <tr>
                                        <td>VENTAS (S/)</td>
                                        <td>
                                            S/<NumberFormat value={funFormatoDecimal(this.state.var_totalVentas, 2)} displayType={'text'} thousandSeparator={true} />
                                        </td>
                                    </tr>
                                    <tr>
                                        <td>N° ITEMS VENDIDOS</td>
                                        <td>{this.state.var_numeroItemsVendidos}</td>
                                        <td 
                                            rowSpan={"2"}
                                            style={{
                                                position:'relative'
                                            }}
                                        >
                                            {/* <div
                                                style={{
                                                    height: "100%",
                                                    width: "100%",
                                                    position: "absolute",
                                                    top: "0",
                                                    display: "flex",
                                                    justifyContent: "center",
                                                    alignItems: "center",
                                                }}
                                            >
                                                <button 
                                                    className="btn btn-rounded btn-fw btn-primary ver" 
                                                    type="button" >
                                                        <i className="mdi mdi-eye"></i>
                                                </button>
                                            </div> */}
                                        </td>
                                    </tr>
                                    <tr>
                                        <td>N° ITEMS CANCELADOS</td>
                                        <td>{this.state.var_numeroItemsCancelados}</td>
                                    </tr>
                                    <tr>
                                        <td>N° TARJETA</td>
                                        <td>{this.state.var_numeroTarjetas}</td>
                                        <td 
                                            rowSpan={"2"}
                                            style={{
                                                position:'relative'
                                            }}
                                        >
                                            {/* <div
                                                style={{
                                                    height: "100%",
                                                    width: "100%",
                                                    position: "absolute",
                                                    top: "0",
                                                    display: "flex",
                                                    justifyContent: "center",
                                                    alignItems: "center",
                                                }}
                                            >
                                                <button 
                                                    className="btn btn-rounded btn-fw btn-primary ver" 
                                                    type="button" >
                                                        <i className="mdi mdi-eye"></i>
                                                </button>
                                            </div> */}
                                        </td>
                                    </tr>
                                    <tr>
                                        <td>TARJETA (S/)</td>
                                        <td>S/<NumberFormat value={funFormatoDecimal(this.state.var_totalTarjetas, 2)} displayType={'text'} thousandSeparator={true} /></td>
                                    </tr>
                                    <tr>
                                        <td>N° EFECTIVO</td>
                                        <td>{this.state.var_numeroEfectivo}</td>
                                        <td 
                                            rowSpan={"2"}
                                            style={{
                                                position:'relative'
                                            }}
                                        >
                                            {/* <div
                                                style={{
                                                    height: "100%",
                                                    width: "100%",
                                                    position: "absolute",
                                                    top: "0",
                                                    display: "flex",
                                                    justifyContent: "center",
                                                    alignItems: "center",
                                                }}
                                            >
                                                <button 
                                                    className="btn btn-rounded btn-fw btn-primary ver" 
                                                    type="button" >
                                                        <i className="mdi mdi-eye"></i>
                                                </button>
                                            </div> */}
                                        </td>
                                    </tr>
                                    <tr>
                                        <td>EFECTIVO (S/)</td>
                                        <td>S/<NumberFormat value={funFormatoDecimal(this.state.var_totalEfectivo, 2)} displayType={'text'} thousandSeparator={true} /></td>
                                    </tr>
                                    <tr>
                                        <td>N° CANCELADOS</td>
                                        <td>{this.state.var_numeroCancelados}</td>
                                        <td 
                                            rowSpan={"2"}
                                            style={{
                                                position:'relative'
                                            }}
                                        >
                                            {/* <div
                                                style={{
                                                    height: "100%",
                                                    width: "100%",
                                                    position: "absolute",
                                                    top: "0",
                                                    display: "flex",
                                                    justifyContent: "center",
                                                    alignItems: "center",
                                                }}
                                            >
                                                <button 
                                                    className="btn btn-rounded btn-fw btn-primary ver" 
                                                    type="button" >
                                                        <i className="mdi mdi-eye"></i>
                                                </button>
                                            </div> */}
                                        </td>
                                    </tr>
                                    <tr>
                                        <td>CANCELADOS (S/)</td>
                                        <td>S/<NumberFormat value={funFormatoDecimal(this.state.var_totalCancelados, 2)} displayType={'text'} thousandSeparator={true} /></td>
                                    </tr>
                                    <tr>
                                        <td>N° GASTOS</td>
                                        <td>{this.state.var_numeroGastos}</td>
                                        <td 
                                            rowSpan={"2"}
                                            style={{
                                                position:'relative'
                                            }}
                                        >
                                            {/* <div
                                                style={{
                                                    height: "100%",
                                                    width: "100%",
                                                    position: "absolute",
                                                    top: "0",
                                                    display: "flex",
                                                    justifyContent: "center",
                                                    alignItems: "center",
                                                }}
                                            >
                                                <button 
                                                    className="btn btn-rounded btn-fw btn-primary ver" 
                                                    type="button" >
                                                        <i className="mdi mdi-eye"></i>
                                                </button>
                                            </div> */}
                                        </td>
                                    </tr>
                                    <tr>
                                        <td>GASTOS (S/)</td>
                                        <td>S/<NumberFormat value={funFormatoDecimal(this.state.var_totalGastos, 2)} displayType={'text'} thousandSeparator={true} /></td>
                                    </tr>
                                    <tr>
                                        <td>N° INGRESOS</td>
                                        <td>{this.state.var_numeroIngresos}</td>
                                        <td 
                                            rowSpan={"2"}
                                            style={{
                                                position:'relative'
                                            }}
                                        >
                                            {/* <div
                                                style={{
                                                    height: "100%",
                                                    width: "100%",
                                                    position: "absolute",
                                                    top: "0",
                                                    display: "flex",
                                                    justifyContent: "center",
                                                    alignItems: "center",
                                                }}
                                            >
                                                <button 
                                                    className="btn btn-rounded btn-fw btn-primary ver" 
                                                    type="button" >
                                                        <i className="mdi mdi-eye"></i>
                                                </button>
                                            </div> */}
                                        </td>
                                    </tr>
                                    <tr>
                                        <td>INGRESOS (S/)</td>
                                        <td>S/<NumberFormat value={funFormatoDecimal(this.state.var_totalIngresos, 2)} displayType={'text'} thousandSeparator={true} /></td>
                                    </tr>
                                    
                                </tbody>
                            </table>
                        </div>
                    </div>
                </div>

                {
                    this.state.var_abrirModalDetalleVenta == true
                    ?<>
                        <div
                            className="Contenedor-Fondo-Modal-ReporteVentas"
                            onClick={() => {
                                this.setState({
                                    var_abrirModalDetalleVenta : false
                                })
                            }}
                        >
                        </div>
                        <div
                            className="Contenedor-Modal-ReporteVentas"
                        >
                            <div
                                className="Modal-ReporteVentas"
                            >

                                <ModalDetalleVenta 
                                    fechaInicio = {this.state.fechaInicio}
                                    fechaFin = {this.state.fechaFin}
                                    cerrar = {() => {
                                        this.setState({
                                            var_abrirModalDetalleVenta : false
                                        })
                                    }}
                                />


                            </div>
                        </div>
                    </>
                    :null
                }


            </div>
        )
    }
}

export default ReporteVentas