import React, {Component} from 'react'
import './RegistrarAlmacen.css'
import config from '../../config'

// const RegistrarAlmacen = () => {
class RegistrarAlmacen extends Component {

    constructor(){
        super();
        this.state ={
            tituloSeleccionado : "0",

            data_tabla_todo : [],
            data_tabla_positivo : [],
            data_tabla_negativo : [],
            data_tabla_cero : [],

            data_tabla_seleccionada : [],

            inp_buscar : ""
            
        }
        this.setTituloSeleccionado = this.setTituloSeleccionado.bind(this)
        this.ObtenerRegistrosAlmacen = this.ObtenerRegistrosAlmacen.bind(this)
    }

    setTituloSeleccionado( titulo ){
        this.setState({
            tituloSeleccionado : titulo
        })

        if(titulo == 0){
            this.setState({
                data_tabla_seleccionada : this.state.data_tabla_todo
            })
        }else if(titulo == 1){
            this.setState({
                data_tabla_seleccionada : this.state.data_tabla_positivo
            })
        }else if(titulo == 2){
            this.setState({
                data_tabla_seleccionada : this.state.data_tabla_negativo
            })
        }else if(titulo == 3){
            this.setState({
                data_tabla_seleccionada : this.state.data_tabla_cero
            })
        }
    }


    ObtenerRegistrosAlmacen(){
        fetch(config.api+`/api/mostrar-registrar-almacen`,
            {
                method: 'POST',
                body: JSON.stringify({
                    
                }),
                headers: {
                    'Accept' : 'application/json',
                    'Content-Type': 'application/json',
                    'usuid'         : localStorage.getItem('usuid')
                }
            }
        )
        .then(response => response.json())
        .then(data => {
            
            
            if(data['response'] == true){
                
                this.setState({
                    data_tabla_todo     : data['todo'],
                    data_tabla_positivo : data['positivos'],
                    data_tabla_negativo : data['negativos'],
                    data_tabla_cero     : data['ceros'],
                })

                if(this.state.tituloSeleccionado == 0){
                    this.setState({
                        data_tabla_seleccionada : data['todo']
                    })
                }else if(this.state.tituloSeleccionado == 1){
                    this.setState({
                        data_tabla_seleccionada : data['positivos']
                    })
                }else if(this.state.tituloSeleccionado == 2){
                    this.setState({
                        data_tabla_seleccionada : data['negativos']
                    })
                }else if(this.state.tituloSeleccionado == 3){
                    this.setState({
                        data_tabla_seleccionada : data['ceros']
                    })
                }


            }else{
                

            }
            
            
            
        })
    }

    componentDidMount(){
        this.ObtenerRegistrosAlmacen()
    }


    render(){
        return(
            <div>
                <div
                    className='Cont-Titulos-RegistrarAlmacen'
                >

                    <div 
                        className='Titulos-RegistrarAlmacen'
                        style={
                            this.state.tituloSeleccionado == 0
                            ?{borderTopLeftRadius  :'8px', color:"white", background:'green'}
                            :{borderTopLeftRadius  :'8px'}
                        }
                        onClick={() =>{
                            this.setTituloSeleccionado(0)
                        }}
                    >
                        Todo
                    </div>
                    <div 
                        className='Titulos-RegistrarAlmacen'
                        style={
                            this.state.tituloSeleccionado == 1
                            ?{color:"white", background:'green'}
                            :{}
                        }
                        onClick={() =>{
                            this.setTituloSeleccionado(1)
                        }}
                    >
                        Positivos
                    </div>
                    <div 
                        className='Titulos-RegistrarAlmacen'
                        style={
                            this.state.tituloSeleccionado == 2
                            ?{color:"white", background:'green'}
                            :{}
                        }
                        onClick={() =>{
                            this.setTituloSeleccionado(2)
                        }}
                    >
                        Negativos
                    </div>
                    <div 
                        className='Titulos-RegistrarAlmacen'
                        style={
                            this.state.tituloSeleccionado == 3
                            ?{borderTopRightRadius  :'8px', color:"white", background:'green'}
                            :{borderTopRightRadius  :'8px'}
                        }
                        onClick={() =>{
                            this.setTituloSeleccionado(3)
                        }}
                    >
                        Cero
                    </div>

                    <div
                        style={{marginLeft:'20px',}}
                    >
                        <input 
                            type        = "text" 
                            className   = "form-control form-control-lg" 
                            name        = "buscar_tb_cliente" 
                            id          = "buscar_tb_cliente"
                            value       = {this.state.inp_buscar}
                            onChange = {(e) => {
                                this.setState({
                                    inp_buscar : e.target.value
                                })
                            }}
                            placeholder='Buscar'
                        />
                    </div>

                    {/* <div>
                        <button
                            type="button" 
                            className="btn btn-success btn-rounded btn-fw desactivado"
                            style={{
                                position: "absolute",
                                right: "32px"
                            }}
                        >
                            Guardar Foto
                        </button>

                    </div> */}


                </div>

                <div>
                    {/* <table>
                        <thead>
                            <th>Codigo</th>
                            <th>Producto</th>
                            <th>Precio</th>
                            <th>Stock</th>
                        </thead>
                        <tbody>
                            <td></td>
                        </tbody>
                    </table> */}

                    {/* <div className="col-lg-12 grid-margin stretch-card"> */}
                        <div className="card">
                            <div className="card-body">
                                <table id="tb_ventas" className="table table-striped Tabla-RegistroAlmacen" style={{width:'100%'}}>
                                    <thead>
                                        <tr>
                                            <th>#</th>
                                            <th>Código</th>
                                            <th>Producto</th>
                                            <th>Stock</th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        {
                                            this.state.data_tabla_seleccionada 
                                            ?this.state.data_tabla_seleccionada.map((task, posicion) =>{
                                                return (
                                                    task.codigo.toLowerCase().includes(this.state.inp_buscar) || task.nombre.toLowerCase().includes(this.state.inp_buscar)
                                                    ?<tr key={task.idVentas}>
                                                        <td>{posicion+1}</td>
                                                        <td>{task.codigo}</td>
                                                        <td>{task.nombre}</td>
                                                        <td>{task.stock}</td>
                                                    </tr>
                                                    :<tr>

                                                    </tr>
                                                );
                                            })
                                            : null
                                        } 
                                    </tbody>
                                </table>
                            </div>
                        </div>
                    {/* </div> */}
                </div>
























            </div>
        )
    }
}

export default RegistrarAlmacen