import React, { Component } from "react";
import config from '../../../../config'
import NumberFormat from 'react-number-format';
import funFormatoDecimal from '../../../../Funciones/funFormatoDecimal'
import comp_paginarTabla from '../../../../Funciones/funPaginarTabla'

class ModalDetalleVenta extends Component {

    constructor(){
        super();
        this.state ={
            venta_tb : []
        }
        this.ObtenerDataDetallada = this.ObtenerDataDetallada.bind(this)
    }

    ObtenerDataDetallada(boolEjem, numero_pagina){
        
        fetch(config.api+`/api/mostrar-detalle-ventas-reporte-ventas`,
            {
                method: 'POST',
                body: JSON.stringify({
                    re_fechainicio : this.props.fechaInicio,
                    re_fechafinal  : this.props.fechaFin,
                    page:numero_pagina
                }),
                headers: {
                    'Accept' : 'application/json',
                    'Content-Type': 'application/json',
                    'usuid'         : localStorage.getItem('usuid')
                }
            }
        )
        .then(response => response.json())
        .then(data => {
            
            this.setState({
                venta_tb : data
            })
            
            
        })

    }

    componentDidMount(){
        this.ObtenerDataDetallada(true, 1)
        
    }



    render(){
        return (
            <div
                style={{
                    position:'relative'
                }}
            >
                <div>
                    <button
                        type="button" 
                        className="btn btn-success btn-rounded btn-fw desactivado"
                        onClick={() => {
                            // this.ObtenerReporteVentas()
                            this.props.cerrar()
                        }}
                        style={{
                            background:'red',
                            position:'absolute',
                            right:'20px'
                        }}
                    >
                        Cerrar
                    </button>
                </div>
                <div
                    style={{
                        overflowY: "auto",
                        height: "530px"
                    }}
                >
                <table id="tb_ventas" className="table table-striped" style={{width:'100%', marginTop:'30px'}}>
                    <thead>
                        <tr>
                            <th>#</th>
                            <th>Fecha Emisión</th>
                            <th>Cliente</th>
                            <th>Tipo Comprobante</th>
                            <th>Número</th>
                            <th>Estado</th>
                            <th>Total</th>
                            <th colSpan="2">Acciones</th>
                        </tr>
                        {
                            this.state.venta_tb.data 
                            ?this.state.venta_tb.data.map((task, posicion) =>{
                                return (
                                    <tr key={task.idVentas}>
                                        <td>{this.state.venta_tb.from + posicion}</td>
                                        <td>{task.fechaVentas}</td>
                                        <td>{task.nombreClientes}</td>
                                        <td>
                                            {task.nombreTiposcomprobante}<br/>
                                            {
                                                task.tipoMoneda_id == 3
                                                ?<div style={{color:'blue'}}>TARJETA</div>
                                                :<div style={{color:'green'}}>EFECTIVO</div>
                                            }
                                        </td>
                                        <td>{task.numeroVentas}</td>
                                        <td>{task.estadoSunatVentas}</td>
                                        <td>
                                            S/<NumberFormat value={funFormatoDecimal(task.totalVentas, 2)} displayType={'text'} thousandSeparator={true} />
                                        </td>

                                        <td>
                                            {
                                                task.estadoSunatVentas == 2
                                                ?<button 
                                                    type="button" 
                                                    className="btn waves-effect waves-light btn-rounded btn-danger">
                                                        VENTA CANCELADA
                                                </button>
                                                :null
                                                // :<EliminarVenta 
                                                //     key                 = {task.idVentas} 
                                                //     idVenta             = {task.idVentas}
                                                //     fetchVentaDataTabla = {this.fetchVentaDataTabla}
                                                // />
                                            }
                                            
                                            <a 
                                                // href={'/ventas/pdf/'+task.idVentas} //ojo
                                                href={config.api+'/ventas/pdf/'+task.idVentas} //ojo
                                                target="_blank"
                                                className="btn btn-rounded btn-fw btn-primary ver" 
                                                type="button" >
                                                    <i className="mdi mdi-eye"></i>
                                            </a>
                                            
                                            {/* <ImprimirVenta 
                                                key                 = {task.idVentas} 
                                                idVenta             = {task.idVentas}
                                                fetchVentaDataTabla = {this.fetchVentaDataTabla}
                                            /> */}
                                            
                                        </td>
                                    </tr>
                                );
                            })
                            : null
                        }

                    </thead>
                </table>
                </div>
                
                <div
                    style={{display:'flex'}}
                >

                    {
                        comp_paginarTabla(
                            this.state.venta_tb.total,
                            this.state.venta_tb.per_page,
                            this.state.venta_tb.current_page,
                            this.ObtenerDataDetallada
                        )
                    }

                </div>
                    {/* <div className="container">
                        <div className="row justify-content-end">
                            <div className="col-4">
                                {
                                    this.state.estadoBoton
                                    ?this.state.estadoBoton.map(task =>{
                                        return (
                                            <button key={task}
                                                className="btn btn-sm btn-secondary editar" 
                                                type="button" 
                                                onClick={()=>this.ObtenerDataDetallada(task)}>{task}</button>

                                        );
                                    })   
                                    : null
                                }
                            </div>
                        </div>
                    </div> */}
                </div>
        )
    }
}

export default ModalDetalleVenta